import { useFormik, Form, FormikProvider } from 'formik';
import eyeFill from '@iconify/icons-eva/eye-fill';
import eyeOffFill from '@iconify/icons-eva/eye-off-fill';
import { FaParagraph } from 'react-icons/fa';
import MultipleSelect from './component/form/multiselect'
import MyUploader from './component/form/dropzone'
// material

// ----------------------------------------------------------------------

const RootStyle = styled(Page)(({ theme }) => ({
  [theme.breakpoints.up('md')]: {
    display: 'flex'
  }
}));

const SectionStyle = styled(Card)(({ theme }) => ({
  width: '100%',
  maxWidth: 464,
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  margin: theme.spacing(2, 0, 2, 2)
}));

const ContentStyle = styled('div')(({ theme }) => ({
  minWidth: '100%',
  margin: 'auto ',
  display: 'flex',
  minHeight: '100vh',
  flexDirection: 'column',
  justifyContent: 'center',
  padding: theme.spacing(4, 0)
}));
const FormStyle = styled('form')(({ theme }) => ({
  minWidth: '100%',
  margin: 'auto ',
  display: 'flex',
  minHeight: '100vh',
  flexDirection: 'column',
  justifyContent: 'center',
  padding: theme.spacing(12, 0)
}));

function AddPostForm() {
  let history = useHistory();
  let { id } = useParams();
  const [image, setImage] = useState([]);
  const [category, setCategory] = useState([]);
  const [initValue, setInitValue] = useState({});
  console.log(category)

  
    useEffect(() => {
      if(id !== undefined){

      fetch(`${HOST}posts/find/${id}`)
      .then(res=>res.json())
      .then(res=>{
        setInitValue(res);
        console.log(res)

      })
      .catch(err=>console.log(err))
      handleReset();
     }
    }, [])

  const RegisterSchema = Yup.object().shape({
    // title: Yup.string()
    //   .min(2, 'Too Short!')
    //   .max(50, 'Too Long!')
    //   .required('First name required'),
    // paragraph: Yup.string().min(2, 'Too Short!').required('Last name required'),
    // email: Yup.string().email('Email must be a valid email address').required('Email is required'),
    // password: Yup.string().required('Password is required')
  });
  const editApi = val => {
    fetch(`${HOST}posts/update`, {
      method: 'PUT',
      body: val
    })
      .then(respose => respose.json())
      .then(doc => {
        console.log(doc)
        handleReset();
        history.push('/admin/post');
      })
      .catch(err => console.log(err))
  } 
  const insertApi = val => {
    fetch(`${HOST}posts/add`, {
      method: 'POST',
      body: val
    })
      .then(respose => respose.json())
      .then(doc => {
        console.log(doc)
        handleReset();
      })
      .catch(err => console.log(err))
  }

  let formik = useFormik({
  
    initialValues: {
      title: '',
      paragraph: '',
      seo_title: '',
      seo_keyword: '',
      seo_description: '',
      slug: '',
    },
    validationSchema: RegisterSchema,
    onSubmit: (value) => {
      const val = value;
      const dataForm = new FormData();
      image.forEach(element => {
        dataForm.append('image', element);
      });
      category.forEach(element => {
          dataForm.append('category', element);
      });
      
      if(id !== undefined){
        const keysEdit = Object.keys(initValue);
        keysEdit.forEach((key) => {
          if(key === 'category'){
            return;
          }
          const keyValue = val[key] || initValue[key]; 

        dataForm.append(key,keyValue);
      });
      dataForm.append('userId',initValue._id)
        editApi(dataForm);
      }else{
        const keys = Object.keys(val);
        keys.forEach((key) => {
          dataForm.append(key,val[key]);
        });
       insertApi(dataForm);
      }
    }
  });

const { errors, touched, handleSubmit, isSubmitting, getFieldProps, handleReset } = formik;


const multiSelectHandler = (data) => {
  setCategory(data)
  console.log('this is ' + data)
}
console.log(initValue)

const dropzoneHandler = (status, meta, file) => {
  if (meta.status === 'done') {
    if (image.indexOf(file) === -1) {
      setImage((image) => [...image, file])
    }
  } else if (meta.status === 'removed') {
    const fileter = image.filter(img => img !== file)
    setImage(fileter)
  }

}

return (
  <FormikProvider value={formik}>
    <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
      <Stack spacing={3}>
        {/* <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}> */}
        <TextField
          fullWidth
          label={initValue.title ?? 'Title'}
          {...getFieldProps('title')}
          error={Boolean(touched.title && errors.title)}
          helperText={touched.title && errors.title}
          placeholder='Title'

        />
        <TextField
          fullWidth
          multiline
          rows={4}
          label={initValue.paragraph ?? 'Paragraph'}
          {...getFieldProps('paragraph')}
          error={Boolean(touched.paragraph && errors.paragraph)}
          helperText={touched.paragraph && errors.paragraph}
          placeholder='Paragraph'
        />
        <MultipleSelect multiSelectHandler={multiSelectHandler} initdata={initValue.category} />
        <MyUploader dropzoneHandler={dropzoneHandler}  />

        <TextField
          fullWidth
          label={initValue.seo_title ?? 'SEO Title'}
          {...getFieldProps('seo_title')}
          error={Boolean(touched.seo_title && errors.seo_title)}
          helperText={touched.seo_title && errors.seo_title}
          placeholder='SEO Title'
        />
        <TextField
          fullWidth
          label={initValue.seo_description ?? 'SEO Description'}
          {...getFieldProps('seo_description')}
          error={Boolean(touched.seo_description && errors.seo_description)}
          helperText={touched.seo_description && errors.seo_description}
          placeholder='SEO Description'

        />
        {id && <TextField
        fullWidth
        label={initValue.slug ?? 'SEO Slug'}
        {...getFieldProps('slug')}
        error={Boolean(touched.slug && errors.slug)}
        helperText={touched.slug && errors.slug}
        placeholder='SEO Slug'

      /> }
        <TextField
          fullWidth
          label={initValue.seo_keyword ?? 'SEO Keyword'}
          {...getFieldProps('seo_keyword')}
          error={Boolean(touched.seo_keyword && errors.seo_keyword)}
          helperText={touched.seo_keyword && errors.seo_keyword}
          placeholder='SEO Keyword'
        />


        <LoadingButton
          fullWidth
          size="large"
          type="submit"
          variant="contained"
          loading={isSubmitting}
        >
         { id ? 'Update' : "Submit" }
        </LoadingButton>
      </Stack>
    </Form>
  </FormikProvider>
);
}






export default function AddUser() {
  let { id } = useParams();

  return (
    <Page title="User | Minimal-UI">
      <Container>
        <Box pt={12}></Box>
        
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4" gutterBottom>
            {id ? 'Edit Post' : 'Add Post' }  
          </Typography>
        </Stack>
        <Card >
        
            <RootStyle>
              <ContentStyle>
                <Container>
                <AddPostForm />
                </Container>
              </ContentStyle>
            </RootStyle>
  
        </Card>




      </Container>
    </Page>
  );
}
