const RootStyle = styled(Page)(({ theme }) => ({
  [theme.breakpoints.up('md')]: {
    display: 'flex'
  }
}));

const SectionStyle = styled(Card)(({ theme }) => ({
  width: '100%',
  maxWidth: 464,
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  margin: theme.spacing(2, 0, 2, 2)
}));

const ContentStyle = styled('div')(({ theme }) => ({
  minWidth: '100%',
  margin: 'auto ',
  display: 'flex',
 
  flexDirection: 'column',
  justifyContent: 'center',
  padding: theme.spacing(4, 0)
}));

function AddPostForm() {
  let { id } = useParams();


  let history = useHistory();

  const [initValue, setInitValue] = useState({
  });

  const RegisterSchema = Yup.object().shape({
    // title: Yup.string()
    //   .min(2, 'Too Short!')
    //   .max(50, 'Too Long!')
    //   .required('First name required'),
    // paragraph: Yup.string().min(2, 'Too Short!').required('Last name required'),
    // email: Yup.string().email('Email must be a valid email address').required('Email is required'),
    // password: Yup.string().required('Password is required')
  });

  const insertApi = val => {
      console.log(val)
    fetch(`${HOST}contactus/add`, {
      method: 'POST',
      body:JSON.stringify(val),
      headers: {
        'Content-Type': 'application/json',
     },
    })
      .then(respose => respose.json())
      .then(doc => {
        console.log(doc)
        handleReset();
      })
      .catch(err => console.log(err))
  }

  const editApi = val => {
    console.log(val)
  fetch(`${HOST}contactus/upd`, {
    method: 'PUT',
    body:JSON.stringify(val),
    headers: {
      'Content-Type': 'application/json',
   },
  })
    .then(respose => respose.json())
    .then(doc => {
      console.log(doc)
      handleReset();
      history.push('/admin/contact');
    })
    .catch(err => console.log(err))
  }


  let formik = useFormik({
  
    initialValues: {
      name: '',
      email: '',
      message: '',
      subject: '', 
    },
    validationSchema: RegisterSchema,
    onSubmit: (value) => {
      const val = value;
      if(id !== undefined){
        editApi({
            // title: val.category_title === '' ? initValue.categoryTitle : val.category_title,
            name: val.name || initValue.name,
            email: val.email || initValue.email,
            subject: val.subject || initValue.subject,
            message: val.message || initValue.message,
            userId: id
         })
      }else{
      insertApi({
          name: val.name,
          email: val.email,
          message: val.message,
          subject: val.subject,
      })
      }
    }
  });


  if(id !== undefined){
    useEffect(() => {
      fetch(`${HOST}contactus/find/${id}`)
      .then(res=>res.json())
      .then(res=>{
          if(res){
        setInitValue(res);
        console.log(res)
        }
      })
      .catch(err=>console.log(err))
      handleReset();

    }, [id])
  }
 

const { errors, touched, handleSubmit, isSubmitting, getFieldProps, handleReset } = formik;

return (
  <FormikProvider value={formik}>
    <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
      <Stack spacing={3}>
        {/* <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}> */}
        <TextField
          fullWidth
          label={initValue.name ?? "Name"}
          {...getFieldProps('name')}
          error={Boolean(touched.name && errors.name)}
          helperText={touched.name && errors.name}
          placeHolder={'Name'}
        />

        <TextField
          fullWidth
          multiline
          rows={4}
          label={initValue.message??"Message" }
          {...getFieldProps('message')}
          error={Boolean(touched.message && errors.message)}
          helperText={touched.message && errors.message}
          placeHolder='Message'
        />
        <TextField
          fullWidth
          label={initValue.email ?? "Email"}
          {...getFieldProps('email')}
          error={Boolean(touched.email && errors.email)}
          helperText={touched.email && errors.email}
          placeHolder={'Email'}
        />
        <TextField
          fullWidth
          label={initValue.subject ?? "Subject"}
          {...getFieldProps('subject')}
          error={Boolean(touched.subject && errors.subject)}
          helperText={touched.subject && errors.name}
          placeHolder={'Subject'}
        />


        <LoadingButton
          fullWidth
          size="large"
          type="submit"
          variant="contained"
          loading={isSubmitting}
        >
          {id ? "Update" : "Submit"}
        </LoadingButton>
      </Stack>
    </Form>
  </FormikProvider>
);
}

export default function AddContact() {
  let { id } = useParams();
  return (
    <Page title="User | Minimal-UI">
      <Container>
        <Box pt={12}></Box>
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4" gutterBottom>
            {id ? "Edit Contact" : "Add Contact"}
          </Typography>
        </Stack>
        <Card >
            <RootStyle>
              <ContentStyle>
                <Container>
                <AddPostForm />
                </Container>
              </ContentStyle>
            </RootStyle>
        </Card>
      </Container>
    </Page>
  );
}
